import React, { useState, useEffect } from "react";
import { InputGroup, ListGroup, FormControl, Container, Row, Col, Card, Accordion, Button, ButtonGroup, ToggleButton } from "react-bootstrap";
import { LangModule, LangModuleMask } from "./LangModule";
import "./App.scss";

// Default source for language module data
const DEFAULT_SOURCE = "https://raw.githubusercontent.com/nilsso/nilsso.github.io/code/static/apps/lang_modules/japanese_modules.json";

// Helper functions
const mask = (length: number, fill: boolean) => Array(length).fill(fill);
const toggle_mask = (mask: Array<boolean>, i: number, value: boolean) => {
    let res = [...mask];
    res[i] = value;
    return res;
};

// Fetch ("GET" request) file from URL and parse a JSON
async function requestJSON(url: string) {
    var module_data;
    await fetch(url)
        .then(response => response.json())
        .then(data => module_data = data)
        .catch(e => alert("Invalid source URL\n" + e));
    if (module_data) {
        return module_data;
    } else {
        return [];
    }
}

function App() {
    // Module source
    const [source, setSource] = useState(DEFAULT_SOURCE);
    // Modules
    const [modules, setModules] = useState(Array<LangModule>(0));
    // Joined module
    const [joined, setJoined] = useState(LangModule.empty());
    // Module masks
    const [enabled, setEnabled] = useState(mask(0, false));
    const [sentences, setSentences] = useState(mask(0, true));
    const [vocabulary, setVocabulary] = useState(mask(0, true));

    // If module source changed build new modules
    useEffect(() => {
        requestJSON(source).then(data => setModules(data.map(json => LangModule.from_json(json))));
    }, [source]);

    // If modules changed update the module masks
    useEffect(() => {
        setEnabled(mask(modules.length, false));
        setSentences(mask(modules.length, true));
        setVocabulary(mask(modules.length, true));
    }, [modules]);

    // If module masks changed update the joined module
    useEffect(() => {
        if (modules.length > 0) {
            setJoined(LangModule.join(modules, new LangModuleMask(enabled, sentences, vocabulary)));
        }
    }, [modules, enabled, sentences, vocabulary]);

    return (
        <Container>
            <Row>
                <GeneratorComponent joined={joined}/>
                <ModulesComponent
                    modules={modules}

                    enabled={enabled}
                    sentences={sentences}
                    vocabulary={vocabulary}

                    setEnabled={setEnabled}
                    setSentences={setSentences}
                    setVocabulary={setVocabulary}
                />
            </Row>
            <Source source={source} setSource={setSource}/>
        </Container>
    );
}

function Source(props: any) {
    const [source, setSource] = [props.source, props.setSource];

    const [value, setValue] = useState(source);

    return (
        <Row>
            <Col className="component">
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            Module Source
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        defaultValue={value}
                        onChange={e => setValue(e.target.value)}
                    />
                    <InputGroup.Append>
                        <Button
                            variant="outline-secondary"
                            onClick={() => setSource(value)}
                        >
                            Load
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </Col>
        </Row>
    );
}

function GeneratorComponent(props: any) {
    const joined = props.joined;

    // Generated sentence or expresson
    const [generated, setGenerated] = useState("...");
    const [meaning, setMeaning] = useState("");
    const [hidden, setHidden] = useState("...");
    const [swap, setSwap] = useState(false);

    return (
        <Col className="component">
            <Card>
                <Card.Header>Generator</Card.Header>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <ButtonGroup>
                            <Button
                                onClick={() => {
                                    let [generated, meaning] = joined.random();
                                    if (generated) {
                                        [generated, meaning] = swap ? [meaning, generated] : [generated, meaning];
                                        setHidden("...");
                                        setGenerated(generated);
                                        setMeaning(meaning);
                                    }
                                }
                                }
                            >
                                Generate
                            </Button>
                            <Button
                                variant="success"
                                onClick={() => setHidden(meaning)}
                            >
                                Reveal
                            </Button>
                            <Button
                                active={swap}
                                variant="outline-secondary"
                                onClick={() => setSwap(!swap)}
                            >
                                Swap
                            </Button>
                        </ButtonGroup>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <p>{generated}</p>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <p>{hidden}</p>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    );
}

function ModulesComponent(props: any) {
    // Modules state
    const modules = props.modules;
    // Mask states
    const [enabled, setEnabled] = [props.enabled, props.setEnabled];
    const [sentences, setSentences] = [props.sentences, props.setSentences];
    const [vocabulary, setVocabulary] = [props.vocabulary, props.setVocabulary];

    // Helper to toggle all modules
    const toggleAll = function(checked: boolean) {
        setEnabled(mask(modules.length, checked));
    }

    let module_components = modules.map((lang_module: LangModule, i: number) => {
        return <LangModuleComponent
            key={i}
            lang_module={lang_module}

            enabled={enabled[i]}
            sentences={sentences[i]}
            vocabulary={vocabulary[i]}

            setEnabled={(checked: boolean) => setEnabled(toggle_mask(enabled, i, checked))}
            setSentences={(checked: boolean) => setSentences(toggle_mask(sentences, i, checked))}
            setVocabulary={(checked: boolean) => setVocabulary(toggle_mask(vocabulary, i, checked))}
        />;
    });

    return (
        <Col className="component">
            <Card>
                <Card.Header>Modules</Card.Header>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <ButtonGroup>
                            <Button
                                variant="primary"
                                onClick={() => toggleAll(true)}
                            >
                                All
                            </Button>
                            <Button
                                variant="danger"
                                onClick={() => toggleAll(false)}
                            >
                                None
                            </Button>
                        </ButtonGroup>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <div id="module-list">{module_components}</div>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    );
}

function LangModuleComponent(props: any) {
    // Modules state
    const lang_module = props.lang_module;
    // Mask states
    const [enabled, setEnabled] = [props.enabled, props.setEnabled];
    const [sentences, setSentences] = [props.sentences, props.setSentences];
    const [vocabulary, setVocabulary] = [props.vocabulary, props.setVocabulary];

    const has_sentences = lang_module.sentences.length > 0;
    const has_vocabulary = Object.entries(lang_module.vocabulary).length > 0;

    return(
        <Accordion className="module">
            <Card>
                <Card.Body id="module-control">
                    <ButtonGroup
                        toggle className="module-controls"
                    >
                        <ToggleButton
                            type="checkbox"
                            variant="outline-primary"
                            size="sm"
                            value={0}
                            checked={enabled}
                            onChange={(e) => setEnabled(e.currentTarget.checked)}
                        >
                            {lang_module.name}
                        </ToggleButton>
                        { has_sentences &&
                            <ToggleButton
                                type="checkbox"
                                variant="outline-secondary"
                                size="sm"
                                value={1}
                                checked={sentences}
                                disabled={!enabled}
                                onChange={(e) => setSentences(e.currentTarget.checked)}
                            >
                                Sentences/Expressions
                            </ToggleButton>
                        }
                        { has_vocabulary &&
                            <ToggleButton
                                type="checkbox"
                                variant="outline-secondary"
                                size="sm"
                                value={2}
                                checked={vocabulary}
                                disabled={!enabled}
                                onChange={(e) => setVocabulary(e.currentTarget.checked)}
                            >
                                Vocabulary
                            </ToggleButton>
                        }
                    </ButtonGroup>
                </Card.Body>
            </Card>
            <Card>
                { has_sentences && <SentenceListComponent lang_module={lang_module} /> }
                { has_vocabulary && <VocabularyListComponent lang_module={lang_module} /> }
            </Card>
        </Accordion>
    );
}

function SentenceListComponent(props: any) {
    const lang_module = props.lang_module;

    const rows = lang_module.sentences.map((s, i) => {
        return (
            <Row key={i} className="module-content-item">
                <Col>{s.sentence_template}</Col>
                <Col>{s.meaning_template}</Col>
            </Row>
        );
    });

    return (
        <React.Fragment>
            <Accordion.Toggle
                as={Card.Header}
                className="small module-contents-header"
                variant="link"
                eventKey="0"
            >
                View Sentences and Expressions
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
                <Card.Body>
                    <Card className="module-content">
                        <Container fluid>
                            {rows}
                        </Container>
                    </Card>
                </Card.Body>
            </Accordion.Collapse>
        </React.Fragment>
    );
}

function VocabularyListComponent(props: any) {
    const lang_module = props.lang_module;

    const token_rows = function(choices) {
        return choices.map(([vocab, meaning], i) => {
            return (
                <Row key={i} className="module-content-item">
                    <Col>{vocab}</Col>
                    <Col>{meaning}</Col>
                </Row>
            );
        });
    }

    const vocabulary_rows = Object.entries(lang_module.vocabulary).map(([token, choices], i) => {
        return (
            <React.Fragment key={i}>
                <ListGroup.Item variant="secondary">{token}</ListGroup.Item>
                <ListGroup.Item className="vocabulary">
                    <Container>
                        {token_rows(choices)}
                    </Container>
                </ListGroup.Item>
            </React.Fragment>
        );
    });

    return (
        <React.Fragment>
            <Accordion.Toggle
                as={Card.Header}
                className="small module-contents-header"
                variant="link"
                eventKey="1"
            >
                View Vocabulary
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="1">
                <Card.Body>
                    <Card className="module-content">
                        <ListGroup variant="flush">
                            {vocabulary_rows}
                        </ListGroup>
                    </Card>
                </Card.Body>
            </Accordion.Collapse>
        </React.Fragment>
    );
}

export default App;
